<template>
  <div>
    <div class="b_img">
      <mt-swipe :auto="2000">
        <mt-swipe-item class="b_img" v-for="banner in bannerImgs" :key="banner.id">
          <img :src="banner.address" alt="">
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div id="news_con">
      <ul id="news" :class="{anim:animate==true}">
        <li v-for='(item, index) in items' :key="index">
          <router-link :to="`/news/${item.conUrl}`">
            {{item.content}}
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: 'home',
  data () {
    return {
      bannerImgs: [],
      animate: false,
      items: [
        {content: '测试数据1', conUrl: 'news_01'},
        {content: '测试数据2', conUrl: 'news_02'},
        {content: '测试数据3', conUrl: 'news_03'}
      ]
    }
  },
  created () {
    this.$http.get('/api/banner').then(response => {
      this.bannerImgs = response.body.data
    }, response => {
      alert('网络错误')
    })
    setInterval(this.scrollText, 2000)
  },
  methods: {
    scrollText () {
      this.animate = !this.animate
      setTimeout(function () {
        this.items.push(this.items[0])
        this.items.shift()
        this.animate = false
      }.bind(this), 500)
    }
  }
}
</script>
<style lang="less">
  .b_img{
    width: 100%;
    height: 4.18rem;
    img{
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .mint-swipe-indicators{
    bottom: 6px;
    .mint-swipe-indicator{
      opacity: 0.5;
    }
  }
  #news_con{
    height: 0.7rem;
    line-height: 0.7rem;
    overflow: hidden;
    padding-left: .3rem;
    border-bottom: 1px solid #f3f3f3;
    transition: all 0.5s linear;
    .anim{
      transition: all 0.5s linear;
      margin-top: -0.7rem;
    }
    li{
      line-height: 0.7rem;
      height: 0.7rem;
      .news_link{
        display: block;
      }
    }
  }
</style>



